.hero__container {
  display: flex;
  flex-direction: column;
  gap: 76px;
}
@media (max-width: 1024px) {
  .hero__container {
    gap: 80px;
  }
}
@media (max-width: 768px) {
  .hero__container {
    padding-block-start: 90px;
    gap: 60px;
  }
}

.hero__digital {
  max-width: 1093px;
}

.hero__digital-title {
  font-family: "Sora", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 72px;
  line-height: 86px;
  letter-spacing: 0px;
  padding-block-end: 16px;
  text-wrap: balance;
}
@media (max-width: 1024px) {
  .hero__digital-title {
    font-size: 64px;
    line-height: 64px;
  }
}
@media (max-width: 768px) {
  .hero__digital-title {
    font-size: 40px;
    line-height: 48px;
  }
}

.hero__digital-title--product {
  color: #88888d;
}

.hero__digital-title--point {
  color: #57efb4;
  margin-inline-start: -15px;
}
@media (max-width: 768px) {
  .hero__digital-title--point {
    margin-inline-start: -10px;
  }
}

.hero__digital-text {
  font-family: "Sora", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: 0px;
  color: #88888d;
  padding-block-end: 32px;
  max-width: 809px;
}
@media (max-width: 768px) {
  .hero__digital-text {
    font-size: 16px;
    line-height: 24px;
  }
}

.button {
  text-align: center;
  background-color: #006ada;
  max-width: 107px;
  font-family: "DM Sans", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0px;
  padding-block: 14px;
  padding-inline: 24px;
  border-radius: 8px;
  white-space: nowrap;
}

@media (max-width: 768px) {
  .hero__button {
    max-width: 100%;
  }
}